import React from 'react';

const Jumbotron = () => 
    <div className="jumbotron">
        <h1>
            <span className="glyphicon glyphicon-list-alt" aria-hidden="true"></span> New York Times Search &amp; Save</h1>
    </div>

export default Jumbotron;


